<template>
    <div class="page-login">
        <div class="login-header">
           <a href="//bj.meituan.com"><img src="//s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png" alt="" class="logo"></a>
        </div>
        <div class="login-panel">
           <div class="banner">
               <img src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg" width="480" height="370" alt="">
           </div>
           <div class="form">
               <h4 v-if="error" class="tips">{{error}}</h4>
               <p>
                   <span>
                       
                   </span>
               </p>
               <el-input :class="{error: error && !userName}"  v-model="userName" placeholder="//" prefix-icon="profile"></el-input>
               <el-input :class="{error: error && !password}" v-model="password" type="password" placeholder=""  prefix-icon="password"></el-input>
                <div class="foot">
                    <a href="#">?</a>            
                </div>
                <el-button type="primary" class="btn-login" @click="submit"></el-button>
                <p class="reg">
                    <span></span>
                    <router-link :to="{name: 'register'}"></router-link>
                </p>
                <div class="oauth-wrapper">
                    <h3 class="title-wrapper"><span class="title"></span></h3>
                    <div class="oauth cf">
                        <a class="oauth__link oauth__link--qq" href="/account/connect/tencent" data-mtevent="{&quot;la&quot;:&quot;oauth/qq&quot;}" target="_blank"></a>
                        <a class="oauth__link oauth__link--weibo" href="/account/connect/sina" data-mtevent="{&quot;la&quot;:&quot;oauth/sina&quot;}" target="_blank"></a>
                    </div>
                </div>

           </div>
        </div>
        <footer>
           <ul>
               <li><a href="#"></a></li>
               <li><a href=""></a></li>
               <li><a href=""></a></li>
               <li><a href=""></a></li>
               <li><a href=""></a></li>
           </ul>
           <p>2018  meituan.com ICP070791 11010502025545</p>
       </footer>
    </div>
</template>
<script>
import api from '@/api/index.js'
export default {
     data(){
         return{
             userName:'',
             password:'',
             error:''
         }
     },
     methods:{
         submit(){
             if(!this.userName){
                 this.error = '';
                 return false
             }
             if(!this.password){
                 this.error = '';
                 return false
             }
           api.login({
               params:{
                  userName:this.userName,
               password:this.password
               }

           }).then(res =>{
               if(res.data.status = 'success'){
                   this.$router.push({name:'index'}),
                   this.$store.commit('setUserName',this.userName)
               }else{
                   this.error = res.data.msg
               }
           })
         }
     }
}
</script>
<style lang="scss">
  @import '@/assets/css/login/index.scss';
</style>


